        <div class="sidebar__item">
          <h5 class="text-18 fw-500 mb-10">Popular Filters</h5>
          <div class="sidebar-checkbox">
            <div
              v-for="popularFilterItem in popularFilterItems"
              class="row items-center justify-between"
